// Main SCSS file
@import "_resets";

$primary-color: #479aa1;
$primary-shade: lighten($primary-color, 10);
$accent-color: #f5aea8;
$accent-shade: lighten($accent-color, 10);
$dark-color: #707070;
$light-color: #f1f1f1;

$header-font-family: 'Megrim', cursive;;
$paragraph-font-family: 'Gotu', sans-serif;;

$light_font: 200;
$regular_font: 400;
$bold_font: 600;
